@font-face {
  font-family: "Golos Text";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/GolosText/GolosText-Regular.woff") format("woff"), url("../fonts/GolosText/GolosText-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Golos Text";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/GolosText/GolosText-DemiBold.woff") format("woff"), url("../fonts/GolosText/GolosText-DemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Merriweather";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Merriweather/Merriweather-Regular.woff") format("woff"), url("../fonts/Merriweather/Merriweather-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Merriweather";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Merriweather/Merriweather-Bold.woff") format("woff"), url("../fonts/Merriweather/Merriweather-Bold.woff2") format("woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button-link {
  padding: 15px 20px;
  text-decoration: none;
  border-radius: 66px;
  display: block;
  width: 335px;
  text-align: center;
  background-color: rgb(31, 31, 31);
  color: rgb(255, 255, 255);
  font-size: 16px;
  font-family: "Golos Text", "Arial", sans-serif;
  box-sizing: border-box;
}

.main-header {
  padding: 19px 0 33px;
}
.main-header__logo {
  margin: 0 0 0 11px;
}

.welcome {
  min-height: 720px;
  position: relative;
  width: 100%;
}
.welcome::before, .welcome::after {
  content: "";
  left: 0;
  top: -87px;
  width: 100%;
  height: calc(100% + 88px);
  position: absolute;
  background: url("../img/back-welcome.png");
  z-index: -2;
}
.welcome::after {
  background: url("../img/image-welcome.png");
  background-repeat: no-repeat;
  z-index: -1;
  margin: 0 auto;
}
.welcome__slogan {
  font-family: "Merriweather", "Arial", sans-serif;
  font-size: 36px;
  text-transform: uppercase;
  width: 335px;
  margin: 0 20px;
  line-height: 115%;
}
.welcome__slogan--t-center {
  margin-left: 61px;
}
.welcome__slogan--t-right {
  margin-left: 29px;
}
.welcome__text {
  font-family: "Golos Text", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 130%;
  width: 335px;
  margin: 22px 20px 0;
}
.welcome__wrap {
  margin: 0;
  padding: 28px 0 0;
}
.welcome__button {
  margin: 0 20px 10px;
}
.welcome__button--more {
  margin: 0 20px;
  background-color: rgb(255, 255, 255);
  color: rgb(31, 31, 31);
}
.welcome__marquee {
  position: absolute;
  bottom: 0;
  background: rgb(245, 73, 50);
  color: rgb(255, 255, 255);
  height: 38px;
  box-sizing: border-box;
  padding: 12px 0;
  text-transform: uppercase;
}

.page-body {
  font-family: "Merriweather", "Arial", sans-serif;
  margin: 0;
}

.info__wrap {
  width: 335px;
  margin: 56px 20px;
}
.info__text {
  font-size: 28px;
  text-transform: uppercase;
  margin: 0;
}